import { Pipe, PipeTransform } from '@angular/core';

@Pipe({ name: 'stringToDate' })
export class StringToDatePipe implements PipeTransform {
  transform(value: string, args: string[]): any {
    if (value) {
      return new Date(value);
    }
  }
}



