import { Component } from '@angular/core';

import { Task } from './../task-service/task';
import { TaskService } from './../task-service/task.service';

@Component({
  selector: 'task-list',
  //template: `<h1>Hello task list</h1>`,
  template: require('./task-list.component.html'),
  styles: [require('./task-list.component.scss')],
  providers: []
})
export class TaskListComponent {
  tasks$;
  task$;
  filteredTasks;
  selectedTask;

  constructor(private taskService: TaskService) { }

  ngOnInit(): void {
    this.tasks$ = this.taskService.getFilteredTasksObservable()
      .subscribe(
      data => {
        this.filteredTasks = data;
        console.log('Task List getFilteredTasksObservable Data: ' + data);
      },
      err => {
        console.log('Task List getFilteredTasksObservable Error: ' + err);
      },
      () => {
        console.log('Task List getFilteredTasksObservable Completed');
      })

    this.task$ = this.taskService.getSelectedTasksObservable()
      .subscribe(
      data => {
        this.selectedTask = data;
        console.log('Task List getSelectedTaskObservable Data: ' + data);
      },
      err => {
        console.log('Task List getSelectedTaskObservable Error: ' + err);
      },
      () => {
        console.log('Task List getSelectedTaskObservable Completed');
      })
  }

  ngOnDestroy(): void {
    this.tasks$.unsubscribe();
    this.task$.unsubscribe();
  }

  onSelect(task) {
    this.taskService.selectTask(task);
  }

  deleteTask(taskid, e) {
    this.taskService.deleteTask(taskid);
  }
}



